<template>
  <div class="home">
    <v-data-table
      :items="data"
      :search="search"
      :headers="headers"
      show-expand
      :single-expand="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-text-field v-model="search" label="Search" outlined class="mt-5" rounded dense />
          <v-spacer></v-spacer>
          <!-- dialog table -->
          <v-dialog max-width="500px" v-model="dialog" persistent>
            <template v-slot:activator="{on}">
              <v-chip v-on="on">Tambah Table</v-chip>
            </template>
            <v-card>
              <v-card-title></v-card-title>
              <v-card-text>
                <v-text-field v-model="edited.name"></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-chip color="red" @click="close">Close</v-chip>
                <v-divider vertical class="mx-4"></v-divider>
                <v-chip color="primary" @click="insert(edited, 'tables')">Save</v-chip>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <!-- dialog kolom -->
          <v-dialog max-width="500px" v-model="edited.dialog" persistent scrollable>
            <v-card>
              <v-card-title>{{edited.name ? `Edit Kolom "${edited.name.toUpperCase()}"` : 'Tambah Kolom'}}</v-card-title>
              <v-card-text>
                <v-text-field v-model="edited.name" label="Name"></v-text-field>
                <v-text-field v-model="edited.size" label="Size"></v-text-field>
                <v-autocomplete
                  :items="typeData"
                  v-model="edited.typeData"
                  item-text="value"
                  item-value="value"
                ></v-autocomplete>
                <v-autocomplete
                  v-if="edited.typeData === 'foreignId'"
                  :items="data"
                  item-text="name"
                  item-value="name"
                  v-model="edited.relasi"
                  label="Relasi Table"
                ></v-autocomplete>
                <v-text-field
                  v-model="edited.relasi_id"
                  label="Relasi id"
                  v-if="edited.typeData === 'foreignId'"
                ></v-text-field>
                <v-autocomplete
                  v-if="edited.typeData === 'foreignId'"
                  :items="modeRelasi"
                  v-model="edited.mode"
                  label="Mode Relasi"
                ></v-autocomplete>
                <v-text-field v-model="edited.enum" label="Enum" v-if="edited.typeData === 'enum'"></v-text-field>
                <v-divider></v-divider>
                <v-row no-gutters>
                  <v-col cols="4">
                    <v-checkbox v-model="edited.image" label="isImage ??"></v-checkbox>
                  </v-col>
                  <v-col cols="4">
                    <v-checkbox v-model="edited.unik" label="isUnik ??"></v-checkbox>
                  </v-col>
                  <v-col cols="4">
                    <v-checkbox v-model="edited.nullable" label="isNull ??"></v-checkbox>
                  </v-col>
                </v-row>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-chip color="red" @click="close">Close</v-chip>
                <v-divider vertical class="mx-4"></v-divider>
                <v-chip color="primary" @click="insert(edited, 'kolom')">Save</v-chip>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.project>
        <v-chip pill color="white" light>
          <v-icon left>mdi-database</v-icon>
          {{$route.params.name}}
        </v-chip>
      </template>
      <template v-slot:item.kolom="{item}">{{item.child.length}} Kolom</template>
      <template v-slot:item.action="{item}">
        <v-chip pill>
          <v-icon left @click="edit(item, 'tables')" color="orange accent-4">mdi-circle-edit-outline</v-icon>
          <v-divider vertical class="mx-2"></v-divider>
          <v-icon @click="destroy(item, 'tables')" color="red">mdi-delete-outline</v-icon>
          <v-divider vertical class="mx-2"></v-divider>
          <v-icon
            right
            @click="edit({table_id: item.id}, 'kolom')"
            color="green accent-4"
          >mdi-plus-circle-outline</v-icon>
        </v-chip>
      </template>
      <!-- expanded -->
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length" style="color:#0000">
          <v-data-table :items="item.child" :headers="headExpand" class="ma-5 elevation-8" light>
            <template v-slot:top></template>
            <template v-slot:item.typeData="{item}">
              <div v-if="item.size !== 'undefined'">{{`${item.typeData} (${item.size})`}}</div>
              <div v-else>{{item.typeData}}</div>
            </template>
            <template v-slot:item.image="{item}">
              <v-icon v-if="item.image === 'true'" color="green">mdi-file-image-outline</v-icon>
              <v-icon v-else color="red">mdi-close-circle-outline</v-icon>
            </template>
            <template v-slot:item.relasi="{item}">
              <v-chip
                pill
                color="green accent-4"
                dark
                v-if="item.relasi !== 'undefined' && item.relasi !== 'false' && item.relasi !== 'null'"
              >
                <v-icon left>mdi-table-search</v-icon>
                {{`${item.relasi} | ${item.mode}`}}
              </v-chip>
              <v-icon v-else color="red">mdi-close-circle-outline</v-icon>
            </template>
            <template v-slot:item.action="{item}">
              <v-chip pill>
                <v-icon left @click="edit(item, 'kolom')">mdi-circle-edit-outline</v-icon>
                <v-divider vertical></v-divider>
                <v-icon right @click="destroy(item, 'kolom')">mdi-delete-outline</v-icon>
              </v-chip>
            </template>
          </v-data-table>
        </td>
      </template>
      <!-- /expanded -->
    </v-data-table>
    <v-snackbar right shaped top v-model="snack.model" color="primary">{{snack.text}}</v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
var query = {
  delete: function(data) {
    return `delete from ${data.table} where id=${data.id}`;
  },
  insert: function(data, table) {
    let value = `'${data.name}','${data.table_id}','${data.typeData}','${data.size}','${data.enum}','${data.image}','${data.relasi}','${data.relasi_id}','${data.nullable}','${data.mode}'`;
    let cols = `'name', 'table_id','typeData','size','enum','image','relasi','relasi_id','nullable','mode'`;

    if (table === "kolom") {
      return `insert into ${table} (${cols}) values (${value})`;
    } else {
      return `insert into ${table} ('name','project_id') values ('${data.name}','${data.project_id}')`;
    }
  },
  update: function(data, table) {
    let value = `'${data.name}','${data.table_id}','${data.typeData}','${data.size}','${data.enum}','${data.image}','${data.relasi}','${data.relasi_id}','${data.nullable}','${data.mode}'`;
    let cols = `'name', 'table_id','typeData','size','enum','image','relasi','relasi_id','nullable','mode'`;

    if (table === "kolom") {
      return `update ${table} set (${cols}) = (${value}) where id = ${data.id}`;
    } else {
      return `update ${table} set ('name','project_id') = ('${data.name}','${data.project_id}') where id = ${data.id}`;
    }
  }
};
export default {
  name: "Home",
  data() {
    return {
      data: [],
      search: null,
      dialog: false,
      snack: {
        model: false,
        text: null
      },
      edited: {},
      index: -1,
      indexCols: -1,
      headers: [
        { text: "ID", value: "id" },
        { text: "Project", value: "project" },
        { text: "Table", value: "name" },
        { text: "Kolom", value: "kolom" },
        { text: "Action", value: "action" }
      ],
      headExpand: [
        { text: "Kolom", value: "name" },
        { text: "Type Data", value: "typeData" },
        // { text: "Size", value: "size" },
        { text: "Relasi", value: "relasi" },
        { text: "Image", value: "image" },
        { text: "Enum", value: "enum" },
        { text: "Action", value: "action" }
      ],
      modeRelasi: [
        "hasMany",
        "belongsTo",
        "hasOne",
        "belongsToMany",
        "hasOneThrough",
        "hasManyThrough",
        "morphTo",
        "morphOne",
        "morphMany",
        "morphToMany",
        "morphedByMany",
        "morphMap"
      ],
      typeData: [
        { text: "_", value: "id" },
        { text: "_", value: "foreignId" },
        { text: "_", value: "bigIncrements" },
        { text: "_", value: "bigInteger" },
        { text: "_", value: "binary" },
        { text: "_", value: "boolean" },
        { text: "_", value: "char" },
        { text: "_", value: "date" },
        { text: "_", value: "dateTime" },
        { text: "_", value: "dateTimeTz" },
        { text: "_", value: "decimal" },
        { text: "_", value: "double" },
        { text: "_", value: "enum" },
        { text: "_", value: "float" },
        { text: "_", value: "string" }, //req other params
        { text: "_", value: "geometry" },
        { text: "_", value: "geometryCollection" },
        { text: "_", value: "increments" },
        { text: "_", value: "integer" },
        { text: "_", value: "ipAddress" },
        { text: "_", value: "json" },
        { text: "_", value: "json" },
        { text: "_", value: "lineString" },
        { text: "_", value: "longText" },
        { text: "_", value: "macAddress" },
        { text: "_", value: "mediumIncrements" },
        { text: "_", value: "mediumInteger" },
        { text: "_", value: "mediumText" },
        { text: "_", value: "morphs" },
        { text: "_", value: "uuidMorphs" },
        { text: "_", value: "multiLineString" },
        { text: "_", value: "multiPoint" },
        { text: "_", value: "softDeletes" },
        { text: "_", value: "text" },
        { text: "_", value: "time" },
        { text: "_", value: "timestamp" },
        { text: "_", value: "tinyInteger" },
        { text: "_", value: "unsignedInteger" },
        { text: "_", value: "unsignedMediumInteger" },
        { text: "_", value: "uuid" },
        { text: "_", value: "year" }
      ]
    };
  },
  watch: {
    "edited.typeData"(val) {
      if (val !== "foreignId") {
        this.edited.relasi = null;
        this.edited.relasi_id = null;
      }
    }
  },
  mounted() {
    const id = this.$route.params.id;
    this.getData(id);
  },
  methods: {
    getData(id) {
      axios
        .get(
          `?view=tables&parent=tables&child=kolom&key=table_id&where=project_id&param=${id}`
        )
        .then(res => {
          this.data = res.data;
          this.snack = {
            model: true,
            text: "Sukses"
          };
        });
    },
    destroy(item, table) {
      let index = this.data.indexOf(item);
      const c = confirm(`Hapus ${table} ?`);
      const token = "ShEAndIyLlu";
      if (c) {
        axios
          .get(`?exec=` + query.delete({ table: table, id: item.id }), {
            headers: {
              Authorization: `Bearer ${token}`
            }
          })
          .then(res => {
            this.snack = {
              model: true,
              text: res.data.message
            };
            this.getData(this.$route.params.id);
          });
      }
    },
    insert(item, table) {
      item.project_id = this.$route.params.id;
      if (this.index > -1 || this.indexCols > -1) {
        //Save new table
        axios
          .get(`?exec=` + query.update(item, table), {
            headers: {
              Authorization: `Bearer sdasjladn`
            }
          })
          .then(res => {
            this.snack = {
              model: true,
              text: res.data.message
            };
            this.getData(this.$route.params.id);
          });
      } else {
        //upadte table
        axios
          .get(`?exec=` + query.insert(item, table), {
            headers: {
              Authorization: `Bearer sdasjladn`
            }
          })
          .then(res => {
            this.snack = {
              model: true,
              text: res.data.message
            };
            this.getData(this.$route.params.id);
          });
      }
    },
    close() {
      this.index = -1;
      this.indexCols = -1;
      this.edited = {};
      this.dialog = false;
    },
    edit(item, type) {
      this.index = this.data.indexOf(item);

      if (type === "kolom") {
        this.indexCols = item.id;
        console.log(item.id);
      }
      this.edited = Object.assign({}, item);
      type === "tables" ? (this.dialog = true) : (this.edited.dialog = true);
    }
  }
};
</script>
